
//svg
//webkit-filter:drop-shadow(-3px -3px 25px red)


path.V {
	animation-name: rotateV;
	animation-duration: 2s;
	transform-origin: 50% 50%; }


@keyframes rotateV {
	0% {
        transform: scale(1.4) rotateY(0deg); }
	30% {
        transform: scale(1.3) rotateY(180deg); }
	100% {
        transform: scale(1) rotateY(0deg); } }




path.F {
	animation-name: rotateF;
	animation-duration: 2s;
	transform-origin: 50% 50%; }

@keyframes rotateF {
	from {
        transform: rotateY(270deg); }
	to {
        transform: rotateY(0deg); } }


path.PD, path.PG {
	animation-name: transPD;
	animation-duration: 3s; }

@keyframes transPD {
	from {
        opacity: 0; }
	to {
        opacity: 1; }

 path.V:hover {
 	opacity: 0.7; } }


.anim2 {
	transform: scale(0.8);
	transform-origin: 30% 30%;
	transition: all 3s; }
