@import "./logo.sass";

//TEST INTEGRATION LOGO
.logo-object {
    width: 100%; }

body {
    margin: 0px; }

//FONTS

@font-face {
    font-family: 'font1';
    src: url("../assets/fonts/HelveticaNeueLTStd-Lt.otf") format("truetype");
    font-weight: normal; }

@font-face {
    font-family: 'font2';
    src: url("../assets/fonts/EncinaScript2_PERSONAL.ttf") format("truetype"); }


//Taille

$hmenu: 15vh;
$l_D: 15%;
$h_D: 85vh;
$h_title: 22%;
$l_title: 12%;

//colors

$color_B: #89ABAD;
$color_R: #BE1E2D;
$color_G: #808080;
$color_G-light: #D1D3D4;
$color_gold: #FFD95B;

/*NAVBAR*/
.grand_container {
    width: 100%;
    height: $hmenu;
    position: fixed;
    right: 0;
    background: white;
    z-index: 20; }



.container_menu {
    width: 80%;
    height: $hmenu;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    align-items: center;
    background: white; }


.vide {
    width: 30%; }

//.menu
////  padding: 2%
////  font-size: 1.2em
////  height: 95px

.menu {
    padding: 2%;
    font-size: 1em;
    text-align: center;
    font-family: 'font1'; }

#name_logo {
    font-family: 'font1';
    font-size: 1.3em;
    color: #666666; }

.menu #name_logo {
    white-space: nowrap; }

.menu-hover {
    border-bottom: 4px solid transparent;
    height: 30px;
    line-height: 30px;
    white-space: nowrap;
    text-transform: uppercase; }

.menu-hover:hover {
    border-bottom: 4px solid $color_R;
    transition: 0.7s; }

.menu-hover:active {
    border-bottom: 4px solid $color_R;
    transition: 0.71s; }

.menu-hover:focus {
    border-bottom: 4px solid $color_R;
    transition: ease 0.7s; }

.logo {
    padding-left: 2%;
    height: 80px;
    width: 80px; }


/*HOMEPAGE SECTION 1*/

.container-section1 {
    width: 100%;
    height: $h_D;
    padding-top: $hmenu;
    background: $color_B;
    background-image: url("../assets/waveL.png"), url("../assets/waveR.png");
    background-repeat: no-repeat, no-repeat;
    background-position: top -10px left -41px, bottom -37px right;
    background-attachment: fixed;
    z-index: 10;
    left: 0;
    position: relative; }

.article_presentation {
    background-image: url("../assets/etoiles.png");
    background-repeat: no-repeat;
    background-size: 110% 110%;
    background-position: 1% 10%;
    width: 40%;
    height: $h_D;
    margin-left: 33.333%;
    background-attachment: fixed; }

.pcfrancois {
    width: 36%;
    padding-left: 8%;
    position: absolute;
    bottom: 0; }

#menu-fixed {
    right: 0px;
    color: white;
    font-size: 1em;
    padding-right: $l_D + 1;
    position: fixed;
    font-family: 'font1';
    margin-top: 1.75em; }

#titleP {
    margin: auto;
    font-size: 7.2em;
    color: white;
    padding-top: 18%;
    padding-left: 2%;
    font-family: 'font2'; }

#lign {
    padding-left: 6.5%;
    position: absolute;
    margin-top: -3.5%;
    width: 32%;
    height: 1.6px; }

#text-homepage {
    color: white;
    padding-left: 29%;
    padding-right: 4%;
    font-family: 'font1';
    text-indent: 5vh;
    font-size: 1em;
    margin-top: -1%;
    width: 62%;
    text-justify: inter-word;
    text-align: justify; }

.first_button {
    margin-left: 29%; }

.button_section1 {
    background-color: $color_G-light;
    color: $color_R;
    border-radius: 25px;
    width: 9em;
    height: 2.8em;
    font-size: 1em;
    border-style: none; }

.button_section1:hover {
    background-color: $color_R;
    color: $color_G-light; }
/*Parallax dans la section 1*/

#boite-light {
    // padding-left: $l_title - 3%
    margin-top: -10.9%;
    height: 50px;
    margin-left: 13%; }

#light {
    background: url("../assets/light-1.png");
    background-repeat: no-repeat;
    background-position: -20% 0;
    background-size: 50%; }


/*NAVBAR DROITE*/

.container_menuD {
    width: $l_D;
    height: $h_D;
    background: $color_G-light;
    position: fixed;
    right: 0;
    z-index: 1;
    top: $hmenu;
    border-left: 1px solid white;
    box-shadow: inset 1em 0em 25px #B3B3B3;
    opacity: 0.9;
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column; }

.list_menuD {
    display: flex;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
    text-decoration: none;
    font-size: 1em;
    color: $color_G;
    font-family: 'font1';
    text-transform: uppercase;
    border-bottom: 1px solid transparent;
    line-height: 15px;
    white-space: nowrap;
    text-transform: uppercase;
    height: $h_D / 9;
    align-items: center;
    position: relative; }


.list_menuD:first-child {
    margin-top: 1vh; }

.list_menuD a {
    margin: auto; }

a:-webkit-any-link {
    text-decoration-line: none;
    color: $color_G; }

a:-webkit-any-link:hover {
    text-decoration-line: none;
    color: white;
    transition: 0.7s; }

.list_menuD:hover {
    border-bottom: 1px solid white;
    width: 100%;
    transition: 1.2s; }

.list_menuDlight:hover::after {
    content: "";
    position: absolute;
    bottom: -25px;
    left: -11px;
    width: 50px;
    height: 50px;
    background: url("../assets/light_menu.png");
    background-repeat: no-repeat;
    background-size: contain;
    z-index: 2;
    opacity: 0.9;
    color: white; }




/*CONTAINER SECTION 2*/

.container-section2 {
    background-image: url("../assets/bg_2.png");
    background-repeat: no-repeat;
    background-size: 100% 112vh; }


.pic_zen {
    background-image: url("../assets/pic_zen.png");
    background-repeat: no-repeat;
    background-size: 48%;
    background-position: top 7em left 14em;
    height: $h_D + 27vh; }

.pic_zen .title_cabinet {
    right: $l_D + 5%;
    font-size: 7em;
    font-family: 'font1';
    font-weight: bold;
    color: $color_gold;
    margin-top: 5%;
    position: absolute; }

.text_cabinet {
    border-right: 1px solid $color_gold;
    color: white;
    padding-bottom: 1em;
    font-family: 'font1';
    right: $l_D + 5%;
    position: absolute;
    margin-top: 12%;
    padding-right: 1em;
    text-align: right;
    font-size: 1.4em;
    line-height: 15px; }

.title_ouverture p {
    margin-bottom: 2em; }

#boite-light2 {
    padding-left: $l_title - 3%;
    height: 50px;
    margin-left: 13%; }


#light_cabinet {
    background: url("../assets/light_cabinet.png");
    background-repeat: no-repeat;
    background-size: 30px; }

#red_cercle {
    background: $color_R;
    border-radius: 50%;
    width: 18em;
    height: 18em;
    z-index: 15;
    position: absolute;
    margin-top: 36%;
    margin-left: 28%;
    display: flex; }

#red_cercle p {
    font-size: 4.5em;
    color: white;
    font-family: 'font2';
    margin-top: 0;
    margin-bottom: 0;
    text-align: center;
    margin: auto; }

/*Effet parallax du cercle*/



#cercle_light {
    background: url("../assets/cercle_halo.png");
    background-repeat: no-repeat;
    background-size: 24em;
    margin-top: 37em;
    margin-left: 66em;
    position: absolute;
    width: 24em;
    height: 24em; }

.reservation {
    font-family: 'font2';
    font-size: 4.5em;
    color: white;
    text-align: center;
    padding-top: 1.8em;
    padding-left: 1.2em;
    position: absolute; }

/*CONTACT*/

/*light parallax*/
#boite-light3 {
    background: url("../assets/cercle_halo.png");
    background-repeat: no-repeat;
    width: 55vw; }


#light3 {
    background: url("../assets/light-1.png");
    background-repeat: no-repeat;
    padding-top: 10vh;
    margin-top: 10.25vh;
    margin-left: 20vw; }

/*boite éléments de contact*/

.boite_link {
    background: url("../assets/bg_3.png");
    background-repeat: no-repeat;
    background-size: 100%;
    height: 44vh;
    padding-top: 2vh; }


.container_contact {
    display: flex;
    width: 70vw;
    margin: auto; }

.contact {
    font-size: 6em;
    color: white; }



.mail-tel {
    font-size: 2em;
    margin: auto; }


.container_border {
    padding-top: 2vh;
    padding-bottom: 2vh;
    border-top: 1px solid white;
    border-bottom: 1px solid white;
    width: 51vw;
    margin: auto; }

.boite_contact {
    width: 50vw;
    height: 15vh;
    background-color: #1A1A1A;
    margin-left: auto;
    margin-right: auto;
    border-top: 1px solid white;
    border-bottom: 1px solid white;
    display: grid;
    grid-template-columns: 0fr 2fr 2fr 0fr 2fr 5fr 0fr;
    grid-gap: 0; }


.boite_adress {
    width: 20vw;
    height: 15vh;
    color: white;
    grid-column: 2 / 3;
    display: flex; }

.adress {
    margin: auto;
    font-family: 'font1';
    font-size: 1.1em; }

.link_adress {
    grid-column: 3 / 4;
    display: flex; }

#y_aller {
    margin: auto;
    color: white;
    font-size: 1.9em; }

#localisation {
    grid-column: 5 / 6;
    border-right: 1px solid white;
    display: flex; }

#img_localisation {
    margin: auto;
    width: 63px; }

.boite_num {
    color: white;
    grid-column: 6 / 7;
    display: flex;
    font-size: 1.7em; }

.num {
    margin: auto; }


/*CONTAINER FOOTER*/

.container-footer {
    // width: 100vw

    background: #1A1A1A;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 0;
    padding-right: 15vw;
    padding-left: 10vw; }


.Lorem {
    grid-column: 1 / 2; }

.ipsum {
    grid-column: 2 / 3; }

.dolor {
    grid-column: 3 / 4; }

.sit {
    grid-column: 4 / 5; }
